<template>
  <div class="repurchase-index">
	<search v-on:sitemSelected="sitemSelected"  :outerShowResult="showResult"></search>
  	<div class="scroll-list"  v-show="showResult">
  		<div class="table-l">
  			<table>
  				<thead>
  					<tr>
  						<td>证券代码</td>
  					</tr>
  				</thead>
  				<tbody>
  					<template v-for="(item, key) in newRepurchaseList">
						<tr class="tr-date">
							<td>{{key}}</td>
						</tr>
						<tr v-for="it in item">
							<td @click="intoStockNativePage(it.Securityshortname,it.Securitycode)">
								{{it.Securityshortname}}
  								<p>{{it.Securitycode}}</p>
							</td>
						</tr>
  					</template>
  				</tbody>
  			</table>
  		</div>
  		<div class="table-r" >
  			<table>
  				<thead>
  					<tr>
  						<td>回购均价(HKD)</td>
  						<td>溢价率</td>
  						<td>回购数量(股)</td>
  						<td>回购占比</td>
  						<td>回购金额(HKD)</td>
  						<td>历史回购</td>
  					</tr>
  				</thead>
  				<tbody>
					<template v-for="(item, key) in newRepurchaseList">
  						<tr class="tr-date">
							<td></td>
						</tr>
						<tr v-for="it in item">
							<td :class="it.Aboverate==0 || it.Aboverate=='--'?'':(it.Aboverate>0?'red':'green')">
								{{it.Purchaseave}}
								<span v-show="it.Currency!='HKD'">(USD)</span>
							</td>
							<td :class="it.Aboverate==0 || it.Aboverate=='--'?'':(it.Aboverate>0?'red':'green')">
								{{it.Aboverate}}
								<span v-show="it.Aboverate!='--'">%</span>
							</td>
							<td>{{it.Purchasevol}}</td>
							<td>{{it.Rate}}%</td>
							<td>
								{{it.Purchaseval}}
								<span v-show="it.Currency!='HKD'">(USD)</span>
							</td>
							<td><router-link tag="a" :to="'/individual/' + it.Securityvarietycode + '/' + it.Securityshortname">历史回购</router-link></td>
						</tr>
  					</template>
  				</tbody>
  			</table>
  		</div>
  	</div>
	<div class="loading-wrap" v-show="isLoading">
  		<i></i>
  		<span>加载中...</span>
  	</div>
	<div class="loaded" v-show="isOver">没有更多数据</div>
	<div class="load-error" v-show="isloadError">加载失败</div>
  </div>
</template>
<script>
const util = require('@/utils/tool');
import search from "./search";
export default {
  name: "index",
  data() {
    return {
      showsearchbox: false,
      searchText: "",
      page: 1,
      size: 20,
      repurchaseList: [],
	  newRepurchaseList: [],
	  isLoading: true,
      isOver: false,
    isLoadingAndRenderDomOver:false,
    isloadError: false,
    	showResult:true

    };
  },
	components:{
		search
	},
  mounted() {
	document.title = "港股回购";
    if(navigator.userAgent.toLocaleLowerCase().indexOf('color=b')>-1 || location.search.indexOf('color=b')>-1
	|| navigator.userAgent.toLocaleLowerCase().indexOf('color=d')>-1 || location.search.indexOf('color=d')>-1){
  		document.documentElement.getElementsByTagName('body')[0].className = '';
  	}
    if(navigator.userAgent.toLocaleLowerCase().indexOf('color=o')>-1 || location.search.indexOf('color=o')>-1){
  		document.documentElement.getElementsByTagName('body')[0].className = 'orange-skin';
  	}
    util.shareFun("港股回购","港股回购-东方财富","东方财富港股回购提供港股交易市场最新最全的回购数据，回购价格、折溢价率等数据轻松获取，并且支持个股历史数据查看。",location.href);
    this.getData(this.page, this.size);
    var that = this;
    window.onscroll = function() {
      var screenHeight = window.screen.height;
      var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
      var scrollHeight = document.body.scrollHeight || document.documentElement.scrollHeight;
      if (screenHeight + scrollTop >= scrollHeight&&that.isLoadingAndRenderDomOver) {
        if (that.isOver) {
          return;
        }
        that.page++;
        that.getData(that.page, that.size);
      }
    };

    // window.BackPage="https://www.baidu.com"

  },
  methods: {
    getData(page, size) {
      this.isLoadingAndRenderDomOver=false
      var that = this;
      this.$http(
        "HG/GetShareRepurchase",
        { pageNum: page, pageSize: size },
        "post"
      ).then(function(data) {
        that.showResult=true
        console.log(data);
        if (data.data.Status == 0) {
			  if (data.data.Result.ShareRepurchaseList.length < that.size) {
				that.isOver = true;
				that.isLoading = false;
			  }
			  that.repurchaseList = that.repurchaseList.concat(
				data.data.Result.ShareRepurchaseList
			  );

			  var newRepurchaseList = {};
			  for (var i in that.repurchaseList) {
				var date = that.repurchaseList[i].Publishdate;
				if (!newRepurchaseList[date]) {
				  newRepurchaseList[date] = [];
				}
				newRepurchaseList[date].push(that.repurchaseList[i]);
			  }
			  that.newRepurchaseList = newRepurchaseList;
			  console.log(that.newRepurchaseList);
        }
		else{
			that.isloadError = true;
			that.isLoading = false;
		}
      }).then(()=>{
        this.isLoadingAndRenderDomOver =true
      });
    },
    cancelBox() {
      this.showsearchbox = false;
      this.searchText = "";
    },
    intoStockNativePage(stockName, stockCode){
      	util.intoStockNativePage(stockName, stockCode,"HK");
    },
     sitemSelected(item) {
			this.showResult=false;
    },
    hideResult(){
      this.showResult=false;
    }
  }
};
</script>



