import Vue from 'vue'
import Router from 'vue-router'
import indexRepurchase from '@/components/repurchase/index'
import individual from '@/components/repurchase/individual'

Vue.use(Router)

export default new Router({
  // base: '/repurchase/',
  routes: [{
      path: '/',
      name: 'indexRepurchase',
      component: indexRepurchase
    },
    {
      path: '/individual/:code/:name?/:search?',
      name: 'individual',
      component: individual
    },
    {
      path: '*',
      redirect: 'indexRepurchase'
    }
  ]
})



