<template>
<div id="search">
<div class="search-wrap" :class="isAbsolute?'abslt':''">
	<div>
		<i class="search-icon"></i>
		<input type="text" v-model="searchText" @input="debounceSearch" @focus="focusSearch" @blur="blurSearch"  placeholder="请输入证券代码/名称"/>
	</div>
	<span class="close" @click="cancelBox"  v-show="showsearchbox">取消</span>
</div>
<ul class="result-list" v-show="showsearchbox">
    <li v-for="(item,index) in searchResult" :key="index" @click="clickItem(item)">
  	    <span v-html="highLight(item.Name)"></span>
  		<span v-html="highLight(item.Code)"></span>
    </li>
</ul>
</div>

</template>

<script>
const util = require('@/utils/tool');
var jsonp = require("jsonp");

export default {
  name: "search",
  data() {
    return {
      showsearchbox: false,
      searchText: "",
      page: 1,
      size: 10,
      searchResult: [],
      timer:null,
	  isAbsolute:false
    };
  },
  props:{
    // 父组件显示搜索结果列表
    outerShowResult:{
      type:Boolean,
      default:true
    }
  },
  watch:{
    outerShowResult(value){
      if(!value){
        this.showsearchbox=false
      }else{
        this.searchText=''
      }
    },
		searchText(value){
			if(value==''){
				document.querySelector('.scroll-list').style.display='block';
			}
		}
  },
  methods: {
    highLight(value) {
      return value.replace(this.searchText, "<i>" + this.searchText + "</i>");
    },
    doSearch(val){
        if(!AG.InApp){
            this.searchUseAjax(val)
        }else{
            this.searchUseNative(val)
        }
    },
    searchUseNative(val){
        eastmoney.stockSearch(JSON.stringify({
            'input':val,
             limit: 50,
             filter:[
                {
                    market: 116,
                    type: [3,4]
                }
            ],


            'callbackName':'stockSearchCallback'
        }))
    },
    searchUseAjax(val) {
      let url = `https://searchapi.eastmoney.com/api/Info/Search?and14=MultiMatch%2fCode%2cName%2cPinYin%7c0%2f${encodeURIComponent(
        val
      )}%5e%2fTrue%26Term%2fClassify%2fHK%5e%2fFalse&isAssociation14=False&pageIndex14=1&pageSize14=10&returnFields14=&type=14&token=CCSDCZSDCXYMYZYYSYYXSMDDSMDHHDJT&callback=aa`;
      jsonp(url,{param: "cb"},(err, data) => {
          if (err) {
            console.error(err.message);
            return;
          }
          this.searchResult = data.Data;

          this.$emit('sitemSelecting')
        }
      );
    },
    debounceSearch() {
        let val = this.searchText;
        if(val != "") {
          this.showsearchbox = true;
					document.querySelector('.scroll-list').style.display='none';
          if(this.timer!=null){
            clearTimeout(this.timer);
          }
          this.timer=setTimeout(()=>this.doSearch(val),300);
        }
		else {
          this.showsearchbox = false;
        }
    },
	focusSearch() {
		this.isAbsolute = true;

		setTimeout(function(){
			document.documentElement.scrollTop = document.body.scrollTop = 0;
		},100);
        this.debounceSearch();
    },
	blurSearch(){
		this.isAbsolute = false;
		document.querySelector('.scroll-list').style.display='block';
	},
    cancelBox() {
      this.showsearchbox = false;
      this.searchText = "";
    },
    clickItem(item){
      this.$emit('sitemSelected',item)
      this.$parent.$router.push({path:`/individual/${item.Code}/${item.Name}/1`})

      // window.location.href=`#/individual/${item.Code}/${item.Name}`
    }
  },
  mounted(){
      window.stockSearchCallback=(result)=>{
          console.log(result)
          let data=JSON.parse(result)
          this.searchResult = data
          console.log(2)

      }

  }
};
</script>



