<template>
    <div class="individual">
        <search v-on:sitemSelected="sitemSelected"  :outerShowResult="showResult"></search>
        <div class="scroll-list" v-show="showResult">
            <div class="table-l">
                <table>
                    <thead>
                        <tr>
                            <td>回购日期</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,index) in repurchaseList" :key="index">
                            <td>{{item.Publishdate}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="table-r">
                <table>
                    <thead>
                        <tr>
                            <td>回购均价(HKD)</td>
                            <td>溢价率</td>
                            <td>回购数量(股)</td>
                            <td>回购占比</td>
                            <td>回购金额(HKD)</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,index) in repurchaseList"   :key="index">
                            <td :class="item.Aboverate==0 || item.Aboverate=='--'?'':(item.Aboverate>0?'red':'green')">
                                {{item.Purchaseave}}
                                <span v-show="item.Currency!='HKD'">(USD)</span>
                            </td>
                            <td :class="item.Aboverate==0 || item.Aboverate=='--'?'':(item.Aboverate>0?'red':'green')">
                                {{item.Aboverate}}
                                <span v-show="item.Aboverate!='--'">%</span>
                            </td>
                            <td>{{item.Purchasevol}}</td>
                            <td>{{item.Rate}}%</td>
                            <td>
                                {{item.Purchaseval}}
                                <span v-show="item.Currency!='HKD'">(USD)</span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="loading-wrap" v-show="isLoading">
            <i></i>
            <span>加载中...</span>
        </div>
        <div class="loaded" v-show="isOver">没有更多数据</div>
        <div class="load-error" v-show="isloadError">加载失败</div>
        <!-- <ul class="result-list" v-show="showsearchbox">
            <li>
                <span>中国钱包</span>
                <span><i>0</i>0802</span>
            </li>
        </ul> -->
    </div>
</template>
<script>
const util = require('@/utils/tool');
import search from './search'
export default {
    name: 'individual',
    data() {
        return {
            showsearchbox: false,
            searchText: '',
            page: 1,
            size: 20,
            repurchaseList: [],
            isOver: false,
            isLoading: true,
            isloadError: false,
            code: this.$route.params.code,
			name: this.$route.params.name,
			showResult:true
        }
    },
    components: {
        search
    },
    mounted() {
        this.setPage()
        this.bindEvents()
        this.getData();

    },
    methods: {
        // 设置页面
        setPage() {
            document.documentElement.scrollTop = document.body.scrollTop = 0;
			document.title = this.name + "-港股回购";
            // 设置页面主题
            if (navigator.userAgent.toLocaleLowerCase().indexOf('color=b') > -1 || location.search.indexOf('color=b') > -1 ||
                navigator.userAgent.toLocaleLowerCase().indexOf('color=d') > -1 || location.search.indexOf('color=d') > -1) {
                document.documentElement.getElementsByTagName('body')[0].className = '';
            }
            // 设置分享函数
            var name = this.name;

            util.shareFun(name + "-港股回购", name + "-港股回购-东方财富", "东方财富港股回购提供港股交易市场最新最全的回购数据，回购价格、折溢价率等数据轻松获取并且支持个股历史数据查看。", location.href);

        },
        // 绑定页面事件
        bindEvents() {
            var that = this;
            window.onscroll = function() {
                var screenHeight = window.screen.height;
                var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
                var scrollHeight = document.body.scrollHeight || document.documentElement.scrollHeight;
                if (screenHeight + scrollTop >= scrollHeight) {
                    if (that.isOver) {
                        return;
                    }
                    that.page++;
                    that.getData();
                }
            }
        },
        getData() {
            let page = this.page
            let size = this.size
            console.log(this.code);
            let that = this;
            this.isLoading = true;
            this.search= this.$route.params.search==1

            this.$http('HG/GetShareRepurchase', {
                securityVarietyCode:that.search?'': that.code,
                securityCode:that.search?that.code:'',
                pageNum: page,
                pageSize: size
            }, 'post').then(function(data) {
              console.log('==========================')
              console.log(data)
				that.showResult=true

                if (data.data.Status == 0) {
                    if (data.data.Result.ShareRepurchaseList.length < that.size) {
                        that.isOver = true;
                        that.isLoading = false;
                    }
                    that.repurchaseList = that.repurchaseList.concat(data.data.Result.ShareRepurchaseList);
                } else {

                    that.isloadError = true;
                    that.isLoading = false;
                }
            });
        },
        cancelBox() {
            this.showsearchbox = false;
            this.searchText = "";
        },
        sitemSelected(item) {
            this.code = item.Code;
            this.name = item.Name;
			this.showResult=false;
			this.repurchaseList =[];
      this.setPage();
      this.$route.params.search=1
      this.page=1;
            this.getData();
        }
    }
}
</script>



